import SingleSelectQuestion from "../../single-select-question/single-select-question";
import { useStateValue } from "../../../state/state";

function Delays() {
  const [
    {
      quiz: { step },
    },
    dispatch,
  ] = useStateValue();

  const onClick = (newDelays) => {
    dispatch({
      type: "setDelays",
      newDelays,
    });
    dispatch({
      type: "setStep",
      newStep: step + 1,
    });
  };

  return (
    <SingleSelectQuestion
      title="How often do you delay what you know you should do?"
      answerOptions={[
        { title: "Almost always", value: "always", icon: "âŒ›" },
        { title: "Sometimes", value: "sometimes", icon: "â³" },
        { title: "Almost never", value: "never", icon: "â±ï¸" },
      ]}
      onClick={onClick}
      eventName="quiz_day_delays"
    />
  );
}

export default Delays;
